import { useState, useEffect } from 'react';
import { supabase } from '../../../lib/supabase';
import { useAuth } from '../../../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';

interface Manual {
  id: string;
  title: string;
  description?: string;
  content_type: 'manual' | 'video';
  url: string;
  created_by: string;
  created_at: string;
  updated_at: string;
}

export default function AdminManualsPage() {
  const { profile, loading: authLoading } = useAuth();
  const navigate = useNavigate();
  const [manuals, setManuals] = useState<Manual[]>([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState<'all' | 'manual' | 'video'>('all');
  const [showForm, setShowForm] = useState(false);
  const [editingId, setEditingId] = useState<string | null>(null);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  const [formData, setFormData] = useState({
    title: '',
    description: '',
    content_type: 'manual' as 'manual' | 'video',
    url: '',
  });

  useEffect(() => {
    // 認証状態の読み込みが完了するまで待つ
    if (authLoading) return;

    if (profile?.role !== 'admin') {
      navigate('/user/dashboard');
      return;
    }
    fetchManuals();
  }, [profile, authLoading]);

  // 認証状態の読み込み中は何も表示しない
  if (authLoading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-blue-500 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">読み込み中...</p>
        </div>
      </div>
    );
  }

  const fetchManuals = async () => {
    try {
      const { data, error } = await supabase
        .from('manuals')
        .select('*')
        .order('created_at', { ascending: false });

      if (error) throw error;
      setManuals(data || []);
    } catch (err) {
      console.error('マニュアル取得エラー:', err);
      setError('マニュアルの取得に失敗しました。');
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    if (!profile) return;

    try {
      if (editingId) {
        // 更新
        const { error } = await supabase
          .from('manuals')
          .update({
            title: formData.title,
            description: formData.description || null,
            content_type: formData.content_type,
            url: formData.url,
            updated_at: new Date().toISOString(),
          })
          .eq('id', editingId);

        if (error) throw error;
        setSuccess('マニュアルを更新しました！');
      } else {
        // 新規登録
        const { error } = await supabase.from('manuals').insert({
          title: formData.title,
          description: formData.description || null,
          content_type: formData.content_type,
          url: formData.url,
          created_by: profile.id,
        });

        if (error) throw error;
        setSuccess('マニュアルを登録しました！');
      }

      // フォームをリセット
      setFormData({
        title: '',
        description: '',
        content_type: 'manual',
        url: '',
      });
      setEditingId(null);
      setShowForm(false);
      fetchManuals();
    } catch (err) {
      console.error('登録エラー:', err);
      setError('登録に失敗しました。もう一度お試しください。');
    }
  };

  const handleEdit = (manual: Manual) => {
    setFormData({
      title: manual.title,
      description: manual.description || '',
      content_type: manual.content_type,
      url: manual.url,
    });
    setEditingId(manual.id);
    setShowForm(true);
    setError('');
    setSuccess('');
  };

  const handleDelete = async (id: string) => {
    if (!confirm('このマニュアルを削除してもよろしいですか？')) return;

    try {
      const { error } = await supabase.from('manuals').delete().eq('id', id);

      if (error) throw error;
      setSuccess('マニュアルを削除しました。');
      fetchManuals();
    } catch (err) {
      console.error('削除エラー:', err);
      setError('削除に失敗しました。');
    }
  };

  const handleCancel = () => {
    setFormData({
      title: '',
      description: '',
      content_type: 'manual',
      url: '',
    });
    setEditingId(null);
    setShowForm(false);
    setError('');
    setSuccess('');
  };

  const filteredManuals = manuals.filter((manual) => {
    if (filter === 'all') return true;
    return manual.content_type === filter;
  });

  if (authLoading || loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-pink-500 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">読み込み中...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <button
                onClick={() => navigate('/admin/dashboard')}
                className="text-gray-600 hover:text-gray-900 transition-colors cursor-pointer"
              >
                <i className="ri-arrow-left-line text-2xl"></i>
              </button>
              <h1 className="text-2xl font-bold text-gray-900">マニュアル管理</h1>
            </div>
            <button
              onClick={() => {
                setShowForm(!showForm);
                if (showForm) handleCancel();
              }}
              className="px-4 py-2 bg-pink-500 text-white rounded-lg hover:bg-pink-600 transition-colors font-medium whitespace-nowrap cursor-pointer"
            >
              {showForm ? 'キャンセル' : '新規登録'}
            </button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {error && (
          <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg text-red-700">
            {error}
          </div>
        )}

        {success && (
          <div className="mb-6 p-4 bg-green-50 border border-green-200 rounded-lg text-green-700">
            {success}
          </div>
        )}

        {showForm && (
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 mb-8">
            <h2 className="text-xl font-bold text-gray-900 mb-6">
              {editingId ? 'マニュアル編集' : 'マニュアル新規登録'}
            </h2>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  タイトル <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  required
                  value={formData.title}
                  onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-transparent text-sm"
                  placeholder="マニュアルのタイトルを入力"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  説明
                </label>
                <textarea
                  value={formData.description}
                  onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                  rows={3}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-transparent text-sm resize-none"
                  placeholder="マニュアルの説明を入力"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  コンテンツタイプ <span className="text-red-500">*</span>
                </label>
                <div className="relative">
                  <select
                    required
                    value={formData.content_type}
                    onChange={(e) =>
                      setFormData({ ...formData, content_type: e.target.value as 'manual' | 'video' })
                    }
                    className="w-full px-4 py-2 pr-8 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-transparent text-sm appearance-none cursor-pointer"
                  >
                    <option value="manual">マニュアルファイル</option>
                    <option value="video">動画コンテンツ</option>
                  </select>
                  <i className="ri-arrow-down-s-line absolute right-3 top-1/2 -translate-y-1/2 pointer-events-none text-gray-500"></i>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  {formData.content_type === 'manual' ? 'ファイルURL' : 'YouTube動画URL'}{' '}
                  <span className="text-red-500">*</span>
                </label>
                <input
                  type="url"
                  required
                  value={formData.url}
                  onChange={(e) => setFormData({ ...formData, url: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-transparent text-sm"
                  placeholder={
                    formData.content_type === 'manual'
                      ? 'https://example.com/manual.pdf'
                      : 'https://www.youtube.com/watch?v=...'
                  }
                />
              </div>

              <div className="flex space-x-3 pt-4">
                <button
                  type="submit"
                  className="flex-1 px-4 py-2 bg-pink-500 text-white rounded-lg hover:bg-pink-600 transition-colors font-medium whitespace-nowrap cursor-pointer"
                >
                  {editingId ? '更新する' : '登録する'}
                </button>
                <button
                  type="button"
                  onClick={handleCancel}
                  className="flex-1 px-4 py-2 bg-gray-200 text-gray-700 rounded-lg hover:bg-gray-300 transition-colors font-medium whitespace-nowrap cursor-pointer"
                >
                  キャンセル
                </button>
              </div>
            </form>
          </div>
        )}

        <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-xl font-bold text-gray-900">登録済みマニュアル</h2>
            <div className="flex space-x-2">
              <button
                onClick={() => setFilter('all')}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors whitespace-nowrap cursor-pointer ${
                  filter === 'all'
                    ? 'bg-pink-500 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                すべて
              </button>
              <button
                onClick={() => setFilter('manual')}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors whitespace-nowrap cursor-pointer ${
                  filter === 'manual'
                    ? 'bg-pink-500 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                ファイル
              </button>
              <button
                onClick={() => setFilter('video')}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors whitespace-nowrap cursor-pointer ${
                  filter === 'video'
                    ? 'bg-pink-500 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                動画
              </button>
            </div>
          </div>

          {filteredManuals.length === 0 ? (
            <div className="text-center py-12">
              <i className="ri-file-list-3-line text-6xl text-gray-300 mb-4"></i>
              <p className="text-gray-500">マニュアルが登録されていません</p>
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {filteredManuals.map((manual) => (
                <div
                  key={manual.id}
                  className="border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow"
                >
                  <div className="flex items-start justify-between mb-3">
                    <div className="flex items-center space-x-2">
                      <div
                        className={`w-10 h-10 rounded-lg flex items-center justify-center ${
                          manual.content_type === 'manual' ? 'bg-blue-100' : 'bg-red-100'
                        }`}
                      >
                        <i
                          className={`${
                            manual.content_type === 'manual'
                              ? 'ri-file-text-line text-blue-600'
                              : 'ri-video-line text-red-600'
                          } text-xl`}
                        ></i>
                      </div>
                      <span
                        className={`text-xs px-2 py-1 rounded-full ${
                          manual.content_type === 'manual'
                            ? 'bg-blue-100 text-blue-700'
                            : 'bg-red-100 text-red-700'
                        }`}
                      >
                        {manual.content_type === 'manual' ? 'ファイル' : '動画'}
                      </span>
                    </div>
                  </div>

                  <h3 className="font-semibold text-gray-900 mb-2 line-clamp-2">{manual.title}</h3>
                  {manual.description && (
                    <p className="text-sm text-gray-600 mb-4 line-clamp-2">{manual.description}</p>
                  )}

                  <div className="flex space-x-2">
                    <button
                      onClick={() => handleEdit(manual)}
                      className="flex-1 px-3 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors text-sm font-medium whitespace-nowrap cursor-pointer"
                    >
                      編集
                    </button>
                    <button
                      onClick={() => handleDelete(manual.id)}
                      className="flex-1 px-3 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors text-sm font-medium whitespace-nowrap cursor-pointer"
                    >
                      削除
                    </button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </main>
    </div>
  );
}
